@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Roboto+Slab&display=swap');

body{
  background-color:#B3ECF2;
  overflow-x: hidden;
}

#containerHambMenu{
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: end;
  align-items: center;
  align-content: center;
  color:azure;
  background-color: #365359;
  border: #365359 1px solid;
  font-family: 'Lobster', cursive;
  font-size:20px;
  border-bottom: 1px solid #E0DEDF;
}

#containerHambMenu img{
  display: none;
  width:8%;
  height:90%;
}

#containerHambMenu p{
  margin: 10px;
}

a{
  color:azure;
  text-decoration: none;
}

#buttonsContainer{
  background: linear-gradient(to bottom,#365359,#B3ECF2); 
  color:#365359;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-content:flex-start;
  width: 207px;
  height:480px;
}

#buttonsContainer a{
  display: flex;
  align-items: center;
  width:100%;
  height:80px;
  text-decoration: none;
  padding: 0;
  margin: 0;
}

.homeButton{
  width:100%;
  height:60%;
  color:#eee;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  font-family: 'Roboto Slab', serif;
  font-size: 20px;
  transition: all 0.3s;
}

.homeButton:hover{
  background-color: #E0DEDF;
  color:#365359;
}

#curBudgetContainer{
  position: absolute;
  left: 17%;
  top:13%;
  display: flex;
  width: 828px;
  flex-direction: row;
  padding: 15px;
  justify-content: space-around;
  align-items: center;
  height: fit-content;
  background-color: #B3ECF2;
  color: black;
  font-family: 'Roboto Slab', serif;
  font-size: 20px;
}

#addingButton{
  position: absolute;
  left: 99%;
  top: -20.2%;
  margin: 25px 0 20px 0;
  height: fit-content;
  width:3%;
  height: fit-content;
  color: #3B7302;
  font-family:'Roboto Slab', serif;
  font-size: 20px;
}

select{
  margin-right: 15px;
  max-width: 250px;
}

option{
  overflow: auto;
}

label{
  margin-right: 15px;
  width: fit-content;
}

input{
  height:22px;
  margin-right: 70px;
  font-family:'Roboto Slab', serif;
  font-size: 17px;
  text-align: center;
  max-width: 150px;
}

.curBudgetChart{
  position: absolute;
  left:17%;
  top: 25%;
  display:none;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  width:70%;
  height:fit-content;
}

.chartShow{ 
  display: flex;
}

td{
  text-align:left;
  padding:0 0 0 15px;
}

.centeredTd{
  text-align: center;
  padding: 0;
}

.cardConsult{
  position: absolute;
  left:17%;
  top:16%;
  background-color:white;
  font-family: 'Roboto Slab', serif;
  border-radius: 15%;
  padding:10px;
}

.cardNotFound{
  position: absolute;
  width:fit-content;
  left:17%;
  top:16%;
  background-color:white;
  font-family: 'Roboto Slab', serif;
  border-radius: 25%;
  padding:10px;
  margin:0;
  z-index: -1;
}

#searchContainer{
  display: flex;
  flex-direction: row;
  align-items: center;
}

#inputSearch{
  margin:0;
  width:127px;
  height: fit-content;
  border-radius: 25px;
  margin-right: 5px;
  font-size: small;
  text-align: left;
  padding-left: 8px;
}

#magGlass{
  width: 45px;
  height: 45px;
}

input[type="number"]::-webkit-outer-spin-button,input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
   margin: 0;
}

#searchContainer a{
  height:fit-content;
}

.disappear{
  display:none;
}

.table-rowClass{
  display:table-row-group !important;
}

#searchButton{
  border-radius: 25px;
  background-color: beige;
}

form{
  display: flex;
}

#clearButton{
  border-radius: 25px;
  background-color: beige;
  height: 31px;
  font-family: 'Roboto Slab', serif;
  width: 245px;
  font-size: 19px;
  margin: 25px 0 25px 0;
}
   
